@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  box-sizing: border-box;
}

/* 智慧仓储主题色彩系统变量 */
:root {
  --bg-warehouse-900: color-mix(in oklab, #2E3B4E 86%, #000 14%);
  --surf-steel-700: #4A5568;
  --text-silver-100: #F7FAFC;
  --accent-logistics-500: #3182CE;
  --accent-automation-500: #38B2AC;
  --accent-warning-500: #ED8936;

  --accent-logistics-500-16: rgba(49,130,206,0.16);
  --accent-logistics-500-08: rgba(49,130,206,0.08);
  --accent-automation-500-16: rgba(56,178,172,0.16);
  --accent-automation-500-08: rgba(56,178,172,0.08);
  --line-omega: color-mix(in oklab, #F7FAFC 15%, #000 85%);
  --text-muted: color-mix(in oklab, #F7FAFC 78%, #000 22%);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: color-mix(in oklab, #2E3B4E 80%, #000 20%); /* 智慧仓储深蓝背景色 */
  color: var(--text-silver-100);
  min-height: 100vh;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* 统一的按钮样式 */
.btn-primary {
  background: var(--accent-logistics-500);
  color: var(--text-silver-100);
  padding: 12px 20px;
  border-radius: 10px;
  font-weight: 600;
  transition: all 0.2s ease;
}
.btn-primary:hover {
  filter: brightness(1.05);
  box-shadow: 0 8px 24px var(--accent-logistics-500-16);
}
.btn-primary:active {
  filter: brightness(0.97);
}
.btn-primary:focus {
  outline: none;
}

/* 统一的卡片样式 - 智慧仓储风磨砂玻璃效果 */
.card-base {
  background: rgba(74, 85, 104, 0.15);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1), 
              0 0 0 0.5px rgba(49, 130, 206, 0.1) inset;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.card-base:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.15), 
              0 0 0 0.5px rgba(49, 130, 206, 0.2) inset;
  background: rgba(74, 85, 104, 0.18);
}

/* 统一的玻璃效果 - 增强版 */
.glass-effect {
  background: rgba(74, 85, 104, 0.12);
  backdrop-filter: blur(30px) saturate(200%);
  -webkit-backdrop-filter: blur(30px) saturate(200%);
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08), 
              0 0 0 0.5px rgba(49, 130, 206, 0.08) inset;
  border-radius: 16px;
}

/* 深度玻璃效果 - 用于重要组件 */
.glass-deep {
  background: rgba(46, 59, 78, 0.08);
  backdrop-filter: blur(40px) saturate(180%) brightness(1.1);
  -webkit-backdrop-filter: blur(40px) saturate(180%) brightness(1.1);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.12),
              0 0 0 0.5px rgba(49, 130, 206, 0.15) inset;
  border-radius: 24px;
}

/* 轻量玻璃效果 - 用于小组件 */
.glass-light {
  background: rgba(247, 250, 252, 0.03);
  backdrop-filter: blur(12px) saturate(150%);
  -webkit-backdrop-filter: blur(12px) saturate(150%);
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.06);
  border-radius: 12px;
}

/*/* 交互态统一样式 */
.focus-accent {
  outline: none;
}

/* 移除所有按钮的边框和轮廓 */
button:focus,
button:focus-visible,
.btn-primary:focus,
.btn-primary:focus-visible,
.btn-glass:focus,
.btn-glass:focus-visible {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}

/*/* 确保导航栏按钮无边框 */
nav button {
  border: none !important;
  outline: none !important;
}

/* 移除所有可能的焦点样式 */
*:focus,
*:focus-visible,
*:focus-within {
  outline: none !important;
}

/* 特别针对导航栏的所有交互元素 */
nav a:focus,
nav button:focus,
nav a:focus-visible,
nav button:focus-visible {
  outline: none !important;
  box-shadow: none !important;
}

/* 保留原有样式以兼容 - 更新为磨砂玻璃风格 */
.glass-card {
  background: rgba(138, 155, 143, 0.15);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1), 
              0 0 0 0.5px rgba(255, 255, 255, 0.1) inset;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.text-gradient {
  background: linear-gradient(135deg, var(--accent-logistics-500) 0%, var(--text-silver-100) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* 隐藏滚动条样式 */
.hide-scrollbar {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
}

.hide-scrollbar::-webkit-scrollbar {
  display: none; /* Chrome, Safari and Opera */
}

/* 按钮玻璃效果 */
.btn-glass {
  background: rgba(49, 130, 206, 0.15);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  color: var(--text-silver-100);
  padding: 12px 24px;
  border-radius: 12px;
  font-weight: 500;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08),
              0 0 0 0.5px rgba(255, 255, 255, 0.08) inset;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-glass:hover {
  background: rgba(49, 130, 206, 0.25);
  transform: translateY(-1px);
  box-shadow: 0 6px 32px rgba(0, 0, 0, 0.12),
              0 0 0 0.5px rgba(255, 255, 255, 0.12) inset;
}

/* 输入框玻璃效果 */
.input-glass {
  background: rgba(49, 130, 206, 0.05);
  backdrop-filter: blur(16px) saturate(150%);
  -webkit-backdrop-filter: blur(16px) saturate(150%);
  border-radius: 12px;
  padding: 12px 16px;
  color: var(--text-silver-100);
  box-shadow: 0 0 0 0.5px rgba(255, 255, 255, 0.05) inset;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.input-glass:focus {
  background: rgba(49, 130, 206, 0.08);
  box-shadow: 0 0 0 2px rgba(49, 130, 206, 0.3),
              0 0 0 0.5px rgba(255, 255, 255, 0.1) inset;
  outline: none;
}
.input-glass::placeholder {
  color: var(--text-muted);
}

/* 导航栏玻璃效果 */
.nav-glass {
  background: rgba(122, 158, 159, 0.02);
  backdrop-filter: blur(24px) saturate(180%) brightness(1.05);
  -webkit-backdrop-filter: blur(24px) saturate(180%) brightness(1.05);
  box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.05) inset;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  /* 移动端减少模糊度以提升性能 */
  .card-base, .glass-effect, .glass-card, .glass-deep {
    backdrop-filter: blur(12px) saturate(150%);
    -webkit-backdrop-filter: blur(12px) saturate(150%);
  }
}

/* 性能优化样式 */
/* 启用GPU加速 */
.gpu-accelerated {
  transform: translateZ(0);
  will-change: transform;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  perspective: 1000px;
}

/* 优化过渡性能 */
* {
  /* 只在需要时使用will-change */
  will-change: auto;
}

/* 简化的模糊效果 - 用于性能优化 */
.glass-effect-light {
  background: rgba(138, 155, 143, 0.12);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  border-radius: 16px;
}

/* 减少动画复杂度 */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    animation-delay: 0.01ms !important;
  }
}

/* 优化卡片悬浮效果 */
.card-optimized {
  transition: transform 0.2s ease-out, box-shadow 0.2s ease-out;
  will-change: auto;
}
.card-optimized:hover {
  transform: translateY(-2px);
  will-change: transform, box-shadow;
}

/* 简化的阴影效果 */
.shadow-optimized {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}
.shadow-optimized-hover:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
} 